$mainColor: rgba(0,0,0,.15);
$linkColor: #FFBF00;
body {
    background: $mainColor;
}
a {
  text-decoration: none;
}
strong {
  font-weight: 900;
}
h1 {
    margin: 20px 0;
    font-family: 'Comic Sans MS';
    font-weight: 900;
    // color: #279BF1;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
    color: black;
    font-size: 25px;
    :not(:last-child):not(:first-child) {
      color: #FFBF00;
    }
    :not(:last-child) {
    font-size: 35px;
    }
}
.container {
  display: block;
  margin: 0 auto;
  width: 1200px;
  height: 100%;
  position: relative;
}

.waves {
  position: absolute;
  width: 35px;
  height: 35px;
  top: 2.5px;
  left: 125px;
}
.waves::after,
.wave-js::after {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    width: 75%;
    height: 75%;
    content: '';
    transform: translate(-50%, -50%);
    animation-name: bullet-waves;
    animation-duration: 2s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
    opacity: 0;
    border: 1px solid black;
    border-radius: 100%;
}
@keyframes bullet-waves
{
    0%
    {
        width: 75%;
        height: 75%;
        opacity: 1;
    }
    50%
    {
        width: 125%;
        height: 125%;
        opacity: 0;
    }
    100%
    {
        width: 75%;
        height: 75%;
    }
}

.header-menu {
  height: 50px;
  width: 100%;
  background: white;
  display: flex;
  align-items: center;
  justify-content: start;
  transition: 0.2s ease-in-out;
}
.header-shadow {
  border-radius: 10px;
  box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  background: white;
  margin-bottom: 10px;
}
.header-navigation {
  height: 100%;
  list-style: none;
  padding: 0 20px;
  li:not(:last-child) {
    transform: translateY(-40%);
    height: 100%;
    width: 110px;
    display: inline-block;
    background: none;
    font-size: 17px;
    a {
      font-family: 'Verdana';
      text-align: start;
      cursor: pointer;
    }
    a:hover{color: $linkColor;}
  }
  li:last-child {
    height: 100%;
    width: 240px;
    display: inline-block;
    background: none;
    a {
      width: 50px;
      display: block;
      float: right;
      cursor: pointer;
      color: black;
    }
  }
}

.header-section {
  width: 200%;
  height: 500px;
  overflow: hidden;
  background-size: cover;
  transition: 0.5s ease-in-out;
  display: flex;
  flex-direction: row;
}
.header-slider__left {
  width: 50%;
  height: 100%;
}
.header-slider__right {
  width: 50%;
  height: 100%;
}
.slider-move {
  transform: translateX(-50%);
}
