.header-inf-container {
  width: 100%;
  height: 80px;
  background: white;
}
.header-information {
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  a,span {
    font-family: 'Verdana';
    text-decoration: none;
    color: black;
    font-size: 16px;
  }
  a:hover {color: $linkColor}
  p {
    margin: 5px 0;
    font-family: 'Verdana';
    font-size: 17px;
    font-weight: 700;
  }
