// --------------------------------------------------------------
.svg-x-left {
  transform: rotateZ(-90deg) translateX(-20%);
}
.svg-x-right {
  transform: rotateZ(90deg) translateX(-20%);
}
.slider-menu {
  position: relative;
  cursor: pointer;
  box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  width: 100%;
  height: 40px;
  margin: 10px 0 20px;
  &:hover{background: $linkColor;}
  svg {
    position: absolute;
    width: 35px;
    height: 35px;
    transition: 0.1s ease-in-out;
  }
  p {
    padding: 8px 40px;
    font-size: 20px;
    font-family: 'Verdana';
    font-weight: 600;
  }
}
.wr-pages-list {
  height: 100%;
  overflow: hidden;
}

.page-slides__left-side {
    margin-right: 10px;
    width: 300px;
    height: max-content;
    background: white;
    transition: 0.2s ease-in-out;
    margin-right: 10px;
    z-index: 666;
    border-radius: 10px;
    padding: 0 90px 0 10px;
    ul {
        list-style: none;
        width: 300px;
        left: 0;
        top: 50px;
        background: white;
        border-radius: 10px;
    }
    a {
      display: block;
      width: 100%;
      height: 100%;
      color: black;
      padding: 7px 18px;
      &:hover {
        cursor: pointer;
        color: white;
      }
    }
    li {
        font-family: 'Verdana';
        height: 30px;
        font-size: 15px;
        border-radius: 10px;
        &:hover {
          background: $linkColor;
        }
    }
}
.menu-hide {
  width: 0px;
  padding: 0;
  border: 0;
  margin: 0;
  height: 0;
  overflow: hidden;
}
.menu-show {
  width: 320px;
  box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}
.slider-menu {
  background: white;
}
.page-sides {
  display: flex;
  flex-direction: row;
}
.page-slides__right-side {
  background: white;
  width: 100%;
  height: 100%;
  box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  padding: 20px;
  margin: 0 0 10px;
    h2 {
      font-family: 'Verdana';
      font-size: 19px;
      text-align: center;
      font-weight: 900;
      margin: 20px;
    }
    p, li, a {
      font-family: 'Verdana';
      font-size: 16px;
      line-height: 20px;
      color: black;
    }
    ul {
      padding: 0 20px 0;
    }
    li {
      list-style: disc;
      padding: 3px;
    }
    h3 {
      font-family: 'Verdana';
      font-size: 17px;
      font-weight: 900;
      margin: 20px 0;
    }
    p {
      margin: 10px 0;
    }
}

