}
.we-are__numbers {
  width: 30%;
  height: 100%;
  ul {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    padding: 0 20px;
  }
  li {
    width: 100%;
    height: 30px;
    padding: 15px;
  }
  a,p,span {
    font-family: 'Verdana';
    font-size: 15px;
    color: black;
  }
  p {
    font-weight: 600;
  }
  a:hover {
